.responsive(@minWidth; @rules) {
	@media (min-width: @minWidth) {
		@rules();
	}
}

main {
	margin-top: 200px;
}

body {
	font-family: 'Montserrat', sans-serif;

	&.custom-background {
		background-repeat: repeat-y !important;
		background-size: 1140px !important;
	}

	&.scroll-down .nav-top-wrapper .header-right-column {
		display: none;
	}

	&.scroll-down .nav-top-wrapper {
		height: 90px;
		opacity: .3;
	}

	&.scroll-down .header-top-logo {
		max-width: 75px;
		margin: 2px;
	}
}

p,
li {
	font-size: .85rem;
	line-height: 1.6rem;
}

.alt-font {
	font-family: 'Just Another Hand', cursive;
}

#menu-social-networks-top {
	.menu-item {
		list-style: none;
		display: inline-block;
	}
}

#menu-social-networks-footer {
	.menu-item {
		font-size: 13px;
		list-style: none;
		text-align: left;
	}
}

.title-section {
	background-image: url(../img/title-underline.svg);
	background-repeat: no-repeat;
	background-position: bottom;
	background-size: 100vw;
	color: var(--gray-dark);
	display: inline-block;
	font-size: 32px;
	font-family: 'Just Another Hand', cursive;
	// font-weight: 700;
	margin: 10px 0 25px;
	padding: 10px 13px 8px 2px;
}

@keyframes title-bg-anim {
	0% {
		background-position-x: 0;
	}

	100% {
		background-position-x: -300px;
	}
}

.subtitle {
	font-size: 36px;
	font-family: 'Just Another Hand', cursive;
	color: var(--gray-dark);
	line-height: 1;
}

.icon-feature-home {
	img {
		width: 70px;
		height: 70px;
		object-fit: contain !important;
	}
}

.quote-dark {
	background-color: #c6eeff;
}

/* HEADER */
.top-main-nav {
	background-color: var(--gray-dark);

	.menu-item {
		position: relative;
		font-family: 'Concert One', cursive;
		color: var(--light);
		display: flex;
		height: 40px;
		align-items: center;
		font-size: 1rem;
		list-style: none;

		&:nth-child(1) {
			--fill: #EB5050;
		}

		&:nth-child(2) {
			--fill: #067730;
		}

		&:nth-child(3) {
			--fill: #4E4EF5;
		}

		&:nth-child(4) {
			--fill: #9c9200;
		}

		&:nth-child(5) {
			--fill: #4fa286;
		}

		&:nth-child(6) {
			--fill: #DC50EB;
		}

		&:nth-child(7) {
			--fill: #850000;
		}

		&:nth-child(8) {
			--fill: #00A2FF;
		}

		&:nth-child(9) {
			--fill: #117406;
		}

		&::before {
			content: " ";
			background-color: var(--fill);
			width: 8px;
			display: inline-block;
			height: 100%;
		}

		&.current-menu-item {
			background-color: var(--fill);
		}

		a {
			padding: 0 10px;
			color: var(--light);
		}
	}
}

.menu-mobile-icon {
	right: 10px;
	top: 40px;
}

.icon-sandwich-back {
	display: none;
}

.nav-top-wrapper {
	z-index: 1000;
	display: flex;
	width: 100%;
	flex-wrap: wrap;
	position: fixed;
	height: 230px;
	background-color: var(--white);
	top: 0;
	left: 0;
	z-index: 3000;
}

.main-header {
	background-color: var(--white);
	display: flex;
	flex-wrap: wrap;
	position: fixed;
	z-index: 1000;
	width: 100%;
	top: 0;

	&.open .top-main-nav-wrapper {
		display: flex !important;
		position: fixed;
		top: 0;
		z-index: 2000;
		width: 100vw;
		height: 100vh;
	}

	&.open .top-main-nav {
		flex-wrap: wrap;
		text-align: left !important;
		justify-content: flex-start !important;
		padding-top: 130px;
		padding-left: 0;
		flex-direction: column;
	}

	&.open .icon-sandwich-back {
		display: block;
	}

	&.open .nav-menu-item {
		width: 100%;
	}

	&.open .header-right-column {
		display: none;
	}

	&.open .nav-top-wrapper {
		height: auto;
		padding-bottom: 0 !important;
		background-color: var(--gray-dark);
	}

	&.open .header-top-logo {
		mix-blend-mode: luminosity;
		margin-bottom: 0;
		max-width: 70px;
	}
}

.header-top-logo {
	max-width: 100px;
	margin: 20px;
	transition: all .5s;
}

.top-main-nav-wrapper {
	background-color: var(--gray-dark);
	width: 100%;
}

.max-width-wrapper {
	max-width: 1200px;
	width: 100%;
	margin: 0 auto;
}

.header-contact {
	margin-left: auto;
	a {
		text-decoration: underline;
		color: var(--dark);
	}
}

.header-right-column {
	display: flex;
}

/* FOOTER */
.wrapper-footer-contact {
	a {
		color: var(--light);
	}
}
.footer-headline-xs {
    top: -165px;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
}
.main-footer {
	margin-top: 180px;
	background-color: var(--gray-dark);

	.menu-item {
		margin: 5px;

		a {
			color: var(--light);
		}
	}
}

.logo-footer {
	max-width: 180px;
	margin-top: -60%;
	position: relative;
}

.footer-menu {
	width: 100%;

	.menu-item {
		list-style: none;
	}
}

.column-center {
	flex-grow: 1;
}

.column-right {
	flex-grow: 1;
	width: 100%;
}

.footer-main-nav {
	columns: 2;
}

.footer-nav-menu-item {
	display: block;
	text-align: center;
	color: var(--light);
	margin: 12px 4px;
	font-size: 12px;
}

.footer-small-box-columns {
	display: flex;
	flex-wrap: wrap;
	width: 100%;
}

.footer-small-box-left {
	width: 100%;
}

.wp-block-latest-posts {
	a {
		margin: 9px 0 0;
		display: block;
		font-size: 28px;
		&:extend(.alt-font);
		color: var(--gray-dark);
	}
	.wp-block-latest-posts__post-date {
		line-height: 1;
	}
	&.is-grid li {
		width: 100%;
		margin: 0 0 20px;

		.wp-block-latest-posts__featured-image img {
			width: 100%;
			border-radius: 10px;
		}
	}

	&.alignleft {
		margin-right: 0;
	}

	.wp-block-latest-posts__featured-image.alignleft {
		margin-right: 0;
	}
}

.home-hand-1,
.home-hand-2 {
	img {
		width: 500px;
		max-width: 100px;
		object-fit: contain !important;

		.responsive(800px, {
				max-width: 150px;
			}

		);

		.responsive(1100px, {
				max-width: 250px;
			}

		);
	}
}

.home-hand-1 {
	left: -50px;
	top: 0;
	transform: translateY(-70px);

	.responsive(800px, {
			left: 0;
		}

	);
}

.home-hand-2 {
	right: 0;
	bottom: 0;
	transform: translateY(130px);
}

